
.appWrap {
  width: 100%;
  height: 100%;
}
nav {
  width: 100%;
  position: fixed;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
nav li {
  display: inline-block;
  float: left;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  height: 1rem;
  line-height: 1rem;
}
nav li a {
  display: inline-block;
  font-size: 0.4rem;
}
.active {
  color: rgb(254, 8, 8)
}



