
.tuijianWrap>div {
  width: 100%;
  margin-bottom: 0.1rem;
  display: inline-block;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0.1rem 0 0.1rem;
}
.newsTitle {
  clear: both;
  font-size: 0.3rem;
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
}
.newsImg {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 0.1rem;
}
.newsImg li {
  display: inline-block;
  float: left;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.newsImg li:nth-child(1) {
  margin-right: 0.1rem;
}
.newsImg li:nth-child(2) {
  margin-right: 0.1rem;
}
.newsImg li img {
  width: 100%;
  height: 100%;
}
.newsAuthor {
  font-size: 0.2rem;
  margin: 0.1rem 0 0.2rem 0;
  color: rgb(165, 169, 175);
  float: left;
  display: inline-block;
}
.newsComment {
  font-size: 0.2rem;
  float: left;
  margin: 0.1rem 0 0.2rem 0.1rem;
  color: rgb(165, 169, 175);
}
.newsTime {
  font-size: 0.2rem;
  float: left;
  color: rgb(165, 169, 175);
  margin: 0.1rem 0 0.2rem 0.1rem;
}
.hide {
  float: right;
  font-size: 0.2rem;
  margin: 0.1rem 0 0.2rem 0.1rem;
  border: 1px solid rgb(165, 169, 175);
  color: rgb(165, 169, 175);
  border-radius: 0.1rem;
  text-align: center;
  width: 0.5rem;
}
.hideNews {
  float: right;
  width: 1rem;
  background: rgb(233, 83, 19);
  border-radius: 0.1rem;
  text-align: center;
  font-size: 0.2rem;
  margin: 0.1rem 0 0.2rem 0.1rem;
  border: 1px solid rgb(233, 83, 19);
  color: rgb(255, 255, 255);
}
.loading-layer {
  clear: both;
}
.listWrap {
  border-bottom: 1px solid rgb(182, 178, 200);
  display: inline-block;
  width: 100%;
}



