
.searchWrap {
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 3000;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0.2rem 0 0.2rem;
}
.header {
  width: 100%;
  height: 1rem;
  border-bottom: 1px solid rgb(210, 205, 205);
  line-height: 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.2rem 0;
}
.goBack {
  font-size: 0.5rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 0.6rem;
  text-align: center;
}
.searchInput {
  -webkit-box-flex: 6;
      -ms-flex: 6;
          flex: 6;
  border: 1px solid rgb(215, 206, 210);
  display: inline-block;
  width: 100%;
  height: 100%;
  background: rgb(255, 255, 255) url(../../../static/img/search.png) no-repeat 0.1rem 50%;
  background-size: auto 90%;
  color: rgb(173, 162, 164);
  font-size: 0.4rem;
  padding-left: 0.8rem;
}
.searchBtn {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #fff;
  border: none;
  font-size: 0.3rem;
  color: rgb(173, 162, 164);
}
.searchHistory {
  width: 100%;
}
.headerHistory {
  position: relative;
  display: inline-block;
  height: 0.8rem;
  width: 100%;
  float: left;
  font-size: 0.4rem;
  line-height: 0.8rem;
  color: rgb(173, 162, 164);
}
.grey {
  width: 100%;
  height: 0.1rem;
  background-color: rgb(201, 201, 201);
  clear: both;
}
.deleate {
