          transform: translate(-50%, -50%)
}
.historyContent {
  clear: both;
  width: 100%;
  height: auto;
}
.historyContent li {
  float: left;
  width: 50%;
  font-size: 0.4rem;
  color: rgb(42, 41, 40);
  border: 1px solid rgb(210, 205, 205);
