          box-sizing: border-box;
  padding-left: 0.2rem;
  height: 0.6rem;
  line-height: 0.6rem;
}
.showYourWant {
  width: 100%;
  height: 0.8rem;
  position: relative;
}
.showYourWantInner {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: inline-block;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.4rem;
  color: rgb(173, 162, 164);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.imgShowYourWant {
  width: 0.5rem;
  height: 0.4rem;
  position: absolute;
  top: 50%;
  left: -0.3rem;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%)
}



