
.newsDetailWrap {
  background-color: rgb(255, 255, 255);
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 2000;
}
.newsDetailTitle {
  width: 100%;
  height: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.1rem;
  font-size: 0.4rem;
}
.newsContent {
  overflow: hidden;
  width: 100%;
  position: absolute;
  bottom: 0;
  top: 1rem;
}
.newsDetailAuthor {
  width: 100%;
  height: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.3rem 0.1rem;
  display: block;
  position: relative;
}
.authorName{
  font-size: 0.3rem;
  float: left;
  width: auto;
  height: 1rem;
  line-height:1rem;
  position: absolute;
  top: 0;
}
.follow{
  font-size: 0.3rem;
  float: right;
  width:1rem;
  text-align: center;
  color: rgb(255, 255, 255);
  background-color: rgb(53, 109, 189);
}
.notfollow{
  font-size: 0.3rem;
  float: right;
  width:1rem;
  text-align: center;
  color: rgb(181, 189, 184);
  background-color:rgb(232, 238, 235);
}
.newsDetailContent {
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0.1rem;
  overflow: auto;
}
.newsDetailContent p {
  font-size: 0.3rem;
  margin-bottom: 0.1rem;
}
.newsDetailContent img {
  width: 100%;
  height: auto;
  max-resolution: 0.1rem;
  margin-bottom: 0.1rem;
}



