
.loginWrap {
  width: 100%;
  height: 2.5rem;
  background-color: rgb(78, 73, 77);
  padding-top: 0.5rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.loginWrap ul {
  height: 1rem;
  text-align: center;
}
.loginWrap ul li {
  display: inline-block;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  height: 1rem;
  width: 1rem;
  position: relative;
  margin: 0 0.2rem
}
.loginWrap ul li img {
  height: 50%;
  width: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%)
}
.moreLogin {
  font-size: 0.3rem;
  color: rgb(163, 162, 193);
  background-color: rgb(37, 38, 35);
  width: 2.5rem;
  height: 0.5rem;
  margin: 0 auto;
  border-radius: 0.2rem;
  text-align: center;
  line-height: 0.5rem;
  margin-top: 0.2rem;
}
.collectWrap {
  width: 100%;
  height: 1.5rem;
  font-size: 1rem;
}
.collectWrap ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
}
.collectWrap ul li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 0.1rem;
}
.collectWrap ul li p{
  width: 100%;
  height: 1rem;
  font-size: 0.3rem;
}
.collectWrap ul li img {
  width: 1rem;
  height: 1rem;
}
.grey{
  width: 100%;
  height: 0.1rem;
  border-top: 1px solid rgb(157, 166, 155);
  border-bottom: 1px solid rgb(157, 166, 155);
  background-color: rgb(199, 207, 201);
}
.myFollow{
  width: 100%;
  position: absolute;
  top: 4.1rem;
  bottom: 1rem;
  font-size: 1rem;
}
.myFollow h3{
  color: rgb(0, 0, 0);
  font-size: 0.5rem;
  text-align: center;
  margin-top: 0.1rem;
}
.myFollow ul li{
  font-size: 0.4rem;
  display: inline-block;
  width: 50%;
  text-align: center;
  margin-bottom: 0.1rem;
}



