
.tabWrap {
  width: 100%;
  height: 0.8rem;
  overflow: hidden;
}
.tabWrap ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  height: 100%;
  width: 200%;
}
.tabWrap ul li {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 0.8rem;
  padding: 0 0.1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}
.tabWrap ul li a {
  font-size: 0.3rem;
}
.active {
  color: rgb(254, 8, 8)
}



